.slick-arrow {
  position: absolute;
  border: none;
  font-size: 0;
  text-indent: -100%;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: cover;
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0;
  top: 50%;
  &:focus {
    outline: none; } }

.slick-next {
  right: -40px;
  background-image: url('data:image/svg+xml;base64,'); }


.slick-prev {
  left: -40px;
  background-image: url('data:image/svg+xml;base64,'); }

.slider {
  &--work {
    @include rmax(sm) {
      padding-top: 60px;
      .slick-prev {
        top: 10px;
        left: 0; }
      .slick-next {
        top: 10px;
        left: 40px; } } } }

